.numbersContainer {
	border: 1px solid var(--border-color-2);
	border-radius: 4px;
	padding: 0 16px;
	margin: 24px 0;
}

.numbersContainer div:first-child {
	border-bottom: 1px solid var(--border-color-2);
}

.numbersContainer>div {
	padding: 16px 0;
}

.cryptoAddress {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	flex: 1;
	padding-right: 10px;
}